import './index.scss';
import PropTypes from 'prop-types';
import React from 'react';

/**
 * Page qui sert  afficher les messages d'rreurs dans les cas suivants :
 * - Serveur hors ligne
 * - Page non autoris (message non implmenter actuellement)
 * - Session expir (message non implmenter actuellement)
 * @param message
 * @return {Element}
 * @constructor
 */
const ErrorPage = ({ message }) => {
  return <h1>{message}</h1>;
};

ErrorPage.propTypes = {
  message: PropTypes.string.isRequired,
};

export default ErrorPage;
